import React, { useState } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import Select from "react-select";
import DragBox from "../DragBox/DragBox";
import SearchAddOns from "./../SearchAddOns/SearchAddOns";
import SelectOptions from "./../SelectOptions/SelectOptions";
import styles from "./Product.module.scss";

export default function Product() {
  const [user, setUser] = useState({
    title: "",
    description: "",
    attributes_name: "",
    attributes_value: "",
    cost: 0,
    regular_price: 0,
    sale_price: 0,
    sku: "",
  });
  let getInputValue = (e) => {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);
    // console.log(myUser);
  };
  const selectOptions = [
    { value: "YES", label: "Yes" },
    { value: "NO", label: "No" },
  ];
  const [userChoice, setUserChoice] = useState("");

  const [updated, setUpdated] = useState("");

  const handleClick = () => {
    setUpdated([user.attributes_name, user.attributes_value]);
  };

  let showAllData = () => {
    console.log(user);
  };

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>IKSIB-Product</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
      <div className={`${styles.container}`}>
        <div className="content m-auto  w-50 p-5">
          <h2>Add Product</h2>
          <div className="product-media card p-3 mb-3 pb-4">
            <p>Media</p>
            <div>
              <DragBox />
            </div>
          </div>
          <div className="product-info card p-3 mb-3">
            <div>
              <label>Title</label>
              <input
                type="text"
                className="form-control"
                onChange={getInputValue}
                name="title"
                placeholder="Product Title"
              />
            </div>
            <div>
              <label>Description</label>
              <textarea
                type="text"
                className="form-control"
                onChange={getInputValue}
                name="description"
                placeholder="Product Description"
              />
            </div>
          </div>
          <div className="product-categories card p-3 mb-3">
            <div className="d-flex justify-content-between">
              <div className="w-50 mx-1">
                <label>Category</label>
                <SelectOptions />
              </div>
              <div className="w-50">
                <label>Sub-Category</label>
                <SelectOptions />
              </div>
            </div>
          </div>
          <div className="product-addons card p-3 mb-3">
            <label>Addons</label>
            <SearchAddOns />
          </div>
          <div className="product-details card p-3 mb-3">
            <div>
              <div className="d-flex justify-content-between align-items-center">
                <p>Available for pick up?</p>
                <Select
                  isClearable={false}
                  className="react-select"
                  classNamePrefix="select"
                  options={selectOptions}
                />
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <p>Does this product have a variants?</p>
                <Select
                  isClearable={false}
                  className="react-select"
                  classNamePrefix="select"
                  options={selectOptions}
                  onChange={(chioce) => setUserChoice(chioce)}
                />
              </div>
            </div>
          </div>
          {userChoice.value === "YES" ? (
            <>
              <div className="product-variants card p-3 mb-3">
                <h6>Variants</h6>
                <div className="d-flex justify-content-between">
                  <div className="w-50 mx-1">
                    <label>Option name(size,color,etc)</label>
                    <input
                      type="text"
                      onChange={getInputValue}
                      name="attributes_name"
                      className="form-control"
                      placeholder="Select Option"
                    />
                  </div>
                  <div className="w-50">
                    <label>Values</label>
                    <input
                      onChange={getInputValue}
                      name="attributes_value"
                      className="form-control"
                      placeholder="Select Value"
                    />
                  </div>
                </div>
                <div className="d-flex justify-content-between mt-3">
                  <hr className="" />
                  <button onClick={handleClick} className="btn btn-info">
                    Save
                  </button>
                </div>
                <div>
                  <h6>Saved Variants</h6>
                  <div className="d-flex flex-column">
                    <input
                      className="form-control"
                      type="text"
                      value={updated}
                    />
                  </div>
                </div>
              </div>
              <Link
                onClick={showAllData}
                to="variations"
                className="btn btn-warning float-end w-25"
              >
                Next
              </Link>
              <div className="clear-fix"></div>
            </>
          ) : (
            <>
              <div className="product-info card p-3 mb-3">
                <h6>Pricing</h6>
                <div>
                  <label>Price</label>
                  <input
                    type="number"
                    placeholder="KWD 0.000"
                    className="form-control"
                    onChange={getInputValue}
                    name="regular_price"
                  />
                </div>
                <div>
                  <label>Sale Price</label>
                  <input
                    type="number"
                    placeholder="KWD 0.000"
                    className="form-control"
                    onChange={getInputValue}
                    name="sale_price"
                  />
                </div>
                <hr />
                <div>
                  <label>Cost Per Item</label>
                  <input
                    type="number"
                    placeholder="KWD 0.000"
                    className="form-control"
                    onChange={getInputValue}
                    name="cost"
                  />
                </div>
              </div>
              <div className="product-info card p-3 mb-3">
                <h6>Inventory</h6>
                <div>
                  <label>SKU (Stock Keeping Unit)</label>
                  <input
                    type="text"
                    className="form-control"
                    onChange={getInputValue}
                    name="sku"
                  />
                </div>
                <hr />
                <div>
                  <h6>Locations</h6>
                  <div className="d-flex flex-wrap">
                    <div className="w-50 ">
                      <label>Location 1</label>
                      <input type="text" className="form-control w-75" />
                    </div>
                    <div className="w-50">
                      <label>Location 2</label>
                      <input type="text" className="form-control w-75" />
                    </div>
                    <div className="w-50">
                      <label>Location 3</label>
                      <input type="text" className="form-control w-75" />
                    </div>
                    <div className="w-50">
                      <label>Location 4</label>
                      <input type="text" className="form-control w-75" />
                    </div>
                  </div>
                </div>
              </div>
              <button
                onClick={showAllData}
                className="btn btn-warning float-end w-25"
              >
                Send
              </button>
              <div className="clear-fix"></div>
            </>
          )}
        </div>
      </div>
    </>
  );
}
