import Multiselect from "multiselect-react-dropdown";
import React, { useContext, useState } from "react";
import { ProductContext } from "./../Context/ProductStore";

export default function SelectOptions() {
  const { myCategory } = useContext(ProductContext);
  const [options] = useState(myCategory);
  return (
    <>
      <Multiselect
        onSelect={(e) => {
          console.log(e);
        }}
        onRemove={(e) => {
          console.log(e);
        }}
        options={options}
        displayValue="category_name"
        showCheckbox
      />
    </>
  );
}
